*, *:before, *:after {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}

html {
  position: relative;
}

body {
  position: relative;
  background-color: #683654;
  background-size: 100% 100%;
  height: 100%;
  color: #fff;
  padding: 40px;
}

.grid + .grid {
  margin-top: 10px;
}

.inner-wrap {
  background: rgba(255, 255, 255, 0.2);
  border: 1px solid rgba(255, 255, 255, 0.15);
  padding: 10px;
  color: rgba(255, 255, 255, 0.6);
  text-align: center;
}

.modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
}

.modal-content {
  background-color: white;
  padding: 20px;
  border-radius: 5px;
}

.modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5); /* Un fondo semitransparente para oscurecer el fondo */
  display: flex;
  justify-content: center;
  align-items: center;
}

.modal-content {
  background-color: white;
  border-radius: 5px;
  padding: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
}

input[type="text"] {
  border: none;
  border-bottom: 2px solid gray; /* Un borde inferior gris para darle estilo */
  margin-right: 10px;
  font-size: 1rem;
  padding: 5px;
  outline: none;
  flex: 1; /* Para que el input ocupe el espacio restante */
}

button {
  border: none;
  border-radius: 5px;
  background-color: blue;
  color: white;
  padding: 10px;
  cursor: pointer;
}

.close-button {
  position: absolute;
  top: 10px;
  right: 10px;
  border: none;
  background-color: transparent;
  font-size: 1.2rem;
  cursor: pointer;
}
 .separacion{
  margin-bottom: 1em;
 }
